import { useEffect, useState, useRef, Suspense, lazy } from "react";

// import {
//   OurPrograms,
//   HeaderAuth,
//   HeaderNoAuth,
//   OurServices,
//   AboutUs,
//   SideBarRight,
//   SideBarLeft,
// } from "./components";
import "slick-carousel/slick/slick.css";
import "slick-carousel/slick/slick-theme.css";
import "aos/dist/aos.css";
import Aos from "aos";
import { Routes, Route } from "react-router-dom";
import { useStore } from "./store";
// import TrainersList from "./components/TrainersList";
// import TrainerPage from "./components/TrainerPage";
import { ReactComponent as ArrowUpIcon } from "./icons/arrow-up.svg";

const OurPrograms = lazy(() => import("./components/OurPrograms"));
const HeaderAuth = lazy(() => import("./components/HeaderAuth"));
const HeaderNoAuth = lazy(() => import("./components/HeaderNoAuth"));
const OurServices = lazy(() => import("./components/OurServices"));
const AboutUs = lazy(() => import("./components/AboutUs"));
const SideBarRight = lazy(() => import("./components/SideBarRight"));
const SideBarLeft = lazy(() => import("./components/SideBarLeft"));
const TrainersList = lazy(() => import("./components/TrainersList"));
const TrainerPage = lazy(() => import("./components/TrainerPage"));

// const checkIfUserUsesMobileOrDesktop = () => {
//   return window?.navigator?.userAgentData?.mobile;
// };

const Loader = () => {
  return (
    <div className="text-center py-5 text-xl font-semibold text-two">Loading ...</div>
  );
};

function App() {
  // const isMobile = useStore((state) => state.isMobile);
  // const setIsMobile = useStore((state) => state.setIsMobile);
  const user = useStore((state) => state.user);
  const [showGoUpButton, setShowGoUpButton] = useState(false);
  const containerRef = useRef();

  const handleScroll = (e) => {
    setShowGoUpButton(e.target.scrollTop > 100 ? true : false);
  };

  // useEffect(() => {
  //   setIsMobile(checkIfUserUsesMobileOrDesktop());
  // }, []);

  // if (isMobile === "Not Detected Yet")
  //   return (
  //     <div className="w-screen h-screen flex items-center justify-center font-bold">
  //       Wait A second We Are Trying To Detect Your Platform
  //     </div>
  //   );
  return (
    <Suspense fallback={<Loader />}>
      {user ? <HeaderAuth /> : <HeaderNoAuth />}

      <div
        onScroll={handleScroll}
        ref={containerRef}
        className="h-[calc(100vh-66px)] bg-five w-full overflow-y-auto scroll-smooth"
      >
        <div className={`${user ? "flex" : ""} flex h-full relative`}>
          {user && (
            <div
              style={{ boxShadow: "0px 0px 4px -2px #061628" }}
              className=" absolute mt-2 left-2 h-[calc(100%-1rem)] bg-white w-[40px]"
            >
              <SideBarLeft />
            </div>
          )}
          <div className="h-full grow">
            <div className={`${user ? "px-14" : "px-0"} w-full h-full`}>
              <Routes>
                <Route path="/ourPrograms" element={<OurPrograms />} />
                <Route path="/ourServices" element={<OurServices />} />
                <Route path="/aboutUs" element={<AboutUs />} />
                <Route path="/contactUs" element={<AboutUs />} />

                <Route path="/login" element={<div>Login Page</div>} />
                <Route path="/signup" element={<div>signup Page</div>} />
                <Route path="/Trainers" element={<TrainersList />} />
                <Route path="/Trainer/:id/" element={<TrainerPage />}>
                  <Route path="book" element={<div>book Page</div>} />
                  <Route path="Service" element={<div>Service Page</div>} />
                  <Route path="Reviews" element={<div>Reviews Page</div>} />
                  <Route path="Photo" element={<div>Photo Page</div>} />
                  <Route path="About" element={<div>About Page</div>} />
                </Route>

                <Route path="/Profile" element={<div>Profile Page</div>} />
                <Route path="/Setting" element={<div>Setting Page</div>} />
                <Route path="/Booking" element={<div>Booking Page</div>} />
                <Route path="/Messsages" element={<div>Messsages Page</div>} />
                <Route path="/Location" element={<div>Location Page</div>} />
                <Route path="/Additions" element={<div>Additions Page</div>} />
              </Routes>
            </div>
          </div>
          {user && (
            <div
              style={{ boxShadow: "0px 0px 4px -2px #061628" }}
              className="  absolute mt-2 right-2 h-[calc(100%-1rem)] bg-white w-[40px]"
            >
              <SideBarRight />
              <abbr
                onClick={() => {
                  containerRef.current.scrollTop = 0;
                }}
                title="Go back Up"
                className={`transition-all duration-700 fixed right-5 rounded-full bg-one p-1 cursor-pointer animate-bounce ${
                  showGoUpButton ? "bottom-4 " : "-bottom-20 "
                }`}
              >
                <ArrowUpIcon className="w-5 h-5 text-white" />
              </abbr>
            </div>
          )}
        </div>
      </div>
    </Suspense>
  );
}

export default App;
